@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.portada {
  text-align: center;
  background-color: rgb(42, 42, 42);
  color: #f7484e;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.desc {
  font-size: 20px;
  padding: 15px;
}
.foto {
  border-radius: 50%;
  width: 300px;
}
.titulo {
  font-size: 34px;
}
.menu {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 40px;
}
.espacio {
  margin: 0 10px;
  font-size: 20px;
}
.item a {
  text-decoration: none;
  color: #f7484e;
  font-size: 20px;
  padding-bottom: 10px;
}
.item a:hover {
  border-bottom: 3px solid white;
}

@media screen and (min-width: 1000px) {
  .resp {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }
  .foto {
    width: 250px;
    margin-right: 80px;
  }
  .titulo {
    margin-top: 90px;
  }
  .espacio {
    margin: 0 30px;
  }
  .desc {
    font-size: 24px;
  }
  .item a{
    font-size: 24px;
  }
  .titulo {
    font-size: 40px;
  }
}
