  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: #f7484e;
  color: #FFF;
}
.descripcion{
  height: 700px;
  padding: 20px 50px;
  font-size: 16px;
  line-height: 35px;
  text-align: center;
  transition: .3s;
}
.subtitulo {
  font-size: 24px;
}
.texto {
  font-size: 24px;
}
.tecnologias {
  display: -webkit-flex;
