  font-size: 28px;
}
.stack {
  width: 480px;
  transition: .3s;
}
.stack:hover {
  width: 510px;
}
.react {
  width: 280px;
  transition: .3s;
}
.react:hover {
  width: 310px;
}
@media screen and (min-width: 700px) {
  .descripcion {
    height: 600px;
    padding: 20px 100px;
    transition: .3s;
  }
  .stack {
    width: 600px;
    transition: .3s;
  }
  .stack:hover {
    width: 650px;
  }
  .react {
    width: 350px;
    transition: .3s;
  }
  .react:hover {
    width: 400px;
  }
}
@media screen and (min-width: 1000px) {
  .descripcion {
    height: 500px;
    padding: 20px 100px;
    transition: .3s;
  }
  .stack {
    width: 700px;
    transition: .3s;
  }
  .stack:hover {
    width: 750px;
  }
  .react {
    width: 350px;
    transition: .3s;
  }
  .react:hover {
    width: 400px;
  }
