.trabajo {
  width: 340px;
  background: white;
  margin-top: 50px;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}
.bgnd-img {
  width: 100%;
  border-radius: 10px 10px 0 0;
}
.trabajo-desc {
  font-size: 20px;
}
.botones {
  margin: 15px 0 20px 0;
}
.btn{
  border: 2px solid #f7484e;
  border-radius: 0.6em;
  color: #f7484e;
  font-size: 16px;
  cursor: pointer;
  margin: 0 10px;
  padding: 1em 0;
  border-color: #f7484e;
  color: #fff;
  box-shadow: 0 0 40px 40px #f7484e inset, 0 0 0 0 #f7484e;
  transition: all 50ms ease-in-out;
}
.btn:hover {
  box-shadow: 0 0 15px 0 #f7484e inset, 0 0 15px 1px #f7484e;
  color: black;
}
.btn a {
  padding: 17px 40px;
  text-decoration: none;
  color: white;
}
.btn a:hover {
  color: black;
}

.trabajos {
  background-color: rgb(42, 42, 42);
